import g from '~/globals';
import C from 'Constants';
import React from 'react';
import { CSSTransitionGroup as ReactCSSTransitionGroup } from 'react-transition-group';
import MooReact from 'MooReact';
import DragDrop from 'DragDrop';

// Note: These are all required here becaus they are later created by key
var menusAvailable = {
    ReactAutocomplete: require('ReactAutocomplete'),
    ReactContextMenuSelectGmailAccount: require('ReactContextMenuSelectGmailAccount'),
    ReactContextMenuInsertLink: require('ReactContextMenuInsertLink'),
    ReactContextMenuContactDetails: require('ReactContextMenuContactDetails'),
    ReactContextMenuMobileOpenLink: require('ReactContextMenuMobileOpenLink'),
    ReactContextMenuDocuments: require('ReactContextMenuDocuments'),
    ReactNotePopup: require('ReactNotePopup'),
    ReactContextMenuDropBucket: require('ReactContextMenuDropBucket'),
    ReactContextMenuAuthorize: require('ReactContextMenuAuthorize'),
    ReactContextMenuMobileFilter: require('ReactContextMenuMobileFilter')
};

var ReactContextMenusMobile = MooReact.createClass({
    displayName: 'ReactContextMenusMobile',
    componentDisplay: C.ComponentDisplay.Everywhere,
    // propTypes:
    // {
    // },
    getState: function getState() {
        return {
            contextMenus: g.menus.contextMenuStack,
            noFade: g.menus.contextMenuNoFade,
            isDragging: DragDrop.isVisible
        };
    },
    close: function close(menu, e) {
        if (g.menus.isContextMenuOpen(menu.fn)) {
            g.menus.closeContextMenu(menu.fn);
        }
        e.preventDefault();
        e.stopPropagation();
    },
    render: function render() {
        var _loopIt = 0;

        var state = this.state;

        var menus = [];
        for (var i = 0; i < state.contextMenus.length; i++) {
            if (_loopIt++ > 5000 && __infLoop && __infLoop(_loopIt)) throw new RangeError();

            var menu = state.contextMenus[i];

            if (!menu.noBackdrop) {
                menus.push(React.createElement('div', { key: 'backdrop_' + i, className: 'contextMenusBackdrop', onTouchStart: this.close.bind(this, menu) }));
            }

            if (menu.fn) {
                menus.push(menu.fn(menu));
            } else if (menu.componentName) {
                menu.key = menu.key || menu.componentName;
                menu.id = menu.id || menu.key;

                var component = menusAvailable[menu.componentName].default;
                menus.push(React.createElement(component, menu));
            }
        }

        var classMenus = g.classNames('contextMenusReact', { 'open': menus.length > 0 }, { 'takeClick': menus.length > 0 && !menu.noBackdrop }, { 'none': menus.length === 0 }, { 'hideBackground': state.noFade || MOBILE && state.isDragging });

        return React.createElement(
            ReactCSSTransitionGroup,
            { className: classMenus, transitionName: 'contextMenuAnim', transitionEnterTimeout: 150, transitionLeaveTimeout: 200 },
            menus
        );
    }
});

export default ReactContextMenusMobile;