import g from '~/globals';
import C from 'Constants';
import platform from 'platform';
import React from 'react';
import Observable from 'Observable';
import MooReact from 'MooReact';

var animTime = 300; // Ensure this matches .fancyCheckbox in fancy.less

var FancyCheckbox = MooReact.createClass({
    displayName: 'FancyCheckbox',
    componentDisplay: C.ComponentDisplay.Everywhere,
    // propTypes:
    // {
    //     id: React.PropTypes.string,
    //     onChange: React.PropTypes.func, // onChange handler
    //     className: React.PropTypes.string,
    //     checked: React.PropTypes.oneOfType([React.PropTypes.bool, Observable]),
    //     radio: React.PropTypes.bool,
    //     error: React.PropTypes.bool,
    //     disabed: React.PropTypes.bool,
    //     canChange: React.PropTypes.bool
    // },
    init: function init() {
        var obs = {
            animCheck: false
        };

        if (this.props.checked instanceof Observable) {
            this.checked = this.props.checked;
            this.trackObservable = true;
        } else {
            obs.checked = this.props.checked || this.props.initialChecked;
        }

        this.createObservables(obs);
    },
    getState: function getState() {
        if (this.trackObservable) {
            return {
                checked: this.checked
            };
        } else {
            return {};
        }
    },
    onClick: function onClick() {
        var props = this.props;

        if (!props.disabled) {
            this.animCheck.set(true);

            setTimeout(function () {
                this.animCheck.set(false);
            }.bind(this), animTime);

            var val = !this.checked.get();

            if (!this.trackObservable || props.setObs) {
                this.checked.set(val);
            }

            if (props.onChange) {
                props.onChange(val);
            }
        }

        if (props.onClick) {
            props.onClick(val);
        }
    },
    getValue: function getValue() {
        return this.checked.get();
    },
    setValue: function setValue(value) {
        return this.checked.set(value);
    },
    resetValue: function resetValue() {
        if (this.props.initialChecked !== undefined) {
            this.checked.set(this.props.initialChecked);
        }
    },
    onKeyDown: function onKeyDown(e) {
        if (e.keyCode === KeyCode.Enter) {
            this.onClick(e);
        }
    },
    onChangeInput: function onChangeInput() {
        // <input> wants a change handler
    },
    componentWillReceiveProps: function componentWillReceiveProps(nextProps) {
        if (!this.trackObservable && nextProps.checked !== undefined && nextProps.checked !== this.state.checked) {
            this.checked.set(nextProps.checked);
        }
    },
    getElement: function getElement() {
        return this.refs.element;
    },
    getChildren: function getChildren() {
        return this.props.children;
    },
    render: function render() {
        var props = this.props,
            checked = this.state.checked,
            styleCheck = {
            background: checked && props.checkedColor,
            width: props.size,
            height: props.size,
            lineHeight: props.size && props.size - 1 + 'px',
            fontSize: props.size && props.size - 4 + 'px'
        };

        if (this.isTrackingObservable) {
            checked = checked.get();
        }

        var cls = g.classNames('fancyCheckbox', { 'flexbox': MOBILE }, { 'checked': checked }, { 'disabled': props.disabled }, { 'animate': this.state.animCheck }, { 'radio': props.radio }, { 'error': props.error }, { 'noBG': props.noBG }, props.className),
            clsText = g.classNames('fancyCheckboxText', { 'f1': MOBILE }),
            clsCheck = g.classNames('fancyCheckboxCheck', { 'c-button-raised-primary': checked && !props.disabled }),
            check = React.createElement('span', { className: clsCheck, style: styleCheck });

        return React.createElement(
            'span',
            { className: cls, onClick: props.canChange !== false ? this.onClick : undefined, id: props.id, style: props.style, tabIndex: props.tabIndex, onKeyDown: this.onKeyDown, ref: 'element' },
            React.createElement('input', { className: 'fancyCheckboxInput', type: 'checkbox', checked: checked, onChange: this.onChangeInput }),
            !MOBILE && check,
            this.props.children && React.createElement(
                'span',
                { className: clsText },
                this.props.children
            ),
            MOBILE && check
        );
    }
});

export default FancyCheckbox;